.recipe {
  border-radius: 10px;
  box-shadow: 0px 5px 20px grey;
  margin: 20px;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  background: white;
  align-items: center;
  min-height: 40%;
  min-width: 30%;
}

.images {
  border-radius: 200px;
  height: 100px;
  width: 100px;
}
